.layout {
  background-color: var(--color-primary-dark);
}

.container {
  position: relative;
}

.main-view {
  margin-right: 0px;
}

.drawer {
  display: none;
}

@media only screen and (min-width: 600px) {
  .main-view {
    margin-right: 50px;
  }

  .drawer {
    display: block;
  }
}



